
.containerWelcome{
    display: flex;
    align-items: center;
    justify-content: center;
    align-items: center;
    min-height: 100vh;
    position: relative;

}
.containerWelcome span{
    font-size: 100pt;
    font-weight: bold;
    position: relative;
    font-family: "Fredoka One";
    cursor: pointer;
    transition: all 300ms ease-in-out ;
    display: "flex";
    justify-content: "center";

}
.containerWelcome span::before{
    content: "--l";
    position: absolute;
    transform: scale(1.7);
    filter: blur(18px);
}
.containerWelcome span::after{
    content: "--l";
    position: absolute;
    top: 80%;
    left: 0;
    filter: blur(32px);
    transform: rotateX(40deg);
}
.containerWelcome span:hover{
    filter: contrast(0%);
    height: 10px;
    transform: scale(0.5);
    transition: all ease .4s;

}
.containerWelcome span:nth-child(1){
    color:#01362d ;
}
.containerWelcome span:nth-child(2){
    color: #035A40;
}
.containerWelcome span:nth-child(3){
    color:#057e59 ;
}
.containerWelcome span:nth-child(4){
    color: #12aa91;
}
.containerWelcome span:nth-child(5){
    color: #0c90b8;
}
.containerWelcome span:nth-child(6){
    color: #19c427;
}
.containerWelcome span:nth-child(7){
    color: #057e59;
}
.containerWelcome span:nth-child(8){
    color: #035A40;
}
.containerWelcome span:nth-child(9){
    color:#035A40 ;
}
.containerWelcome span:nth-child(10){
    color:#01362d ;
}


@media (max-width: 990px){
    .containerWelcome span{
        font-size: 60pt;
        font-weight: bold;
        position: relative;
        font-family: "Fredoka One";
        align-items: center;
        cursor: pointer;
        transition: all 300ms ease-in-out ;
        justify-content: "center";
    
    }

    @media (max-width: 770px){
        .containerWelcome span{
            font-size: 55pt;
            font-weight: bold;
            position: relative;
            font-family: "Fredoka One";
            cursor: pointer;
            transition: all 300ms ease-in-out ;
            justify-content: "center";
        
        }

        @media (max-width: 650px){
            .containerWelcome span{
                font-size: 37pt;
                font-weight: bold;
                position: relative;
                font-family: "Fredoka One";
                cursor: pointer;
                transition: all 300ms ease-in-out ;
                justify-content: "center";
            
            }

            @media (max-width: 500px){
                .containerWelcome span{
                    font-size: 27pt;
                    font-weight: bold;
                    position: relative;
                    font-family: "Fredoka One";
                    cursor: pointer;
                    transition: all 300ms ease-in-out ;
                    justify-content: "center";
                
                }
            
            }
        
        }
    
    }

}